<template>
  <div class="cart">
    <app-header
      title="购物车"
      showBack="true"
    ></app-header>
    <div class="cart-main">
      <cart-item
        v-for="(item, index) in cartList"
        :key="index"
        :index="index"
        :product="item"
      ></cart-item>
      <div class="price">
        <span>合计: <em>{{totalFee}}</em></span>
        <span>积分: <em class="not">{{points}}</em></span>
      </div>
      <div class="btn-box">
        <a class="btn" :class="{'n-btn': !totalFee}" @click="bill" href="javascript:void(0)">结算</a>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import CartAddressPopup from './cart-address-popup.vue'
import AddressAdd from '../../home/address/address-add.vue'
import CartItem from './cart-item.vue'
import bus from '../../../assets/js/bus'
import {getCartProductList, removeGoodsOfCart, mallOrderGetPrice} from '../../../api/mall'
import {setLocalStorage} from '../../../lib/util'
export default {
  name: 'Mall-Cart',
  data () {
    return {
      cartList: [],
      playList: [],
      addressList: [],
      popupVisible: false,
      addAddressViewState: false,
      address: '',
      addressID: '',
      totalFee: 0,
      discountFee: 0,
      points: 0
    }
  },
  created () {
    this.getDatas()
  },
  destroyed () {
    bus.$off('addCart')
  },
  mounted () {
    this.$on('removeProductInCart', obj => {
      removeGoodsOfCart(obj.detail_id)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg)
            this.getDatas()
          } else {
            this.$toast(data.errormsg)
          }
        })
    })
    this.$on('selectOneProduct', (item, index, state) => {
      if (state) {
        let datas = {}
        datas[item.detail_id] = item.amount
        this.playList.push(datas)
      } else {
        let _playList = this.playList
        for (let i in _playList) {
          for (let j in _playList[i]) {
            if (Number(j) === Number(item.detail_id)) {
              this.playList.splice(i, 1)
            }
          }
        }
      }
    })
    bus.$on('subNum', (obj, index) => {
      let count = Number(this.cartList[index].amount)
      if (count <= 0) {
        return this.$set(this.cartList[index], 'amount', 0)
      }
      this.$set(this.cartList[index], 'amount', count - 1)
    })
    bus.$on('addNum', (obj, index) => {
      let count = Number(this.cartList[index].amount)
      this.$set(this.cartList[index], 'amount', count + 1)
    })
  },
  watch: {
    playList: function (newValue) {
      if (!newValue.length) {
        this.$set(this, 'totalFee', 0)
        this.$set(this, 'discountFee', 0)
        this.points = 0
        return
      }
      let datas = {}
      for (let i in newValue) {
        for (let j in newValue[i]) {
          datas[j] = newValue[i][j]
        }
      }
      mallOrderGetPrice(JSON.stringify(datas))
        .then(data => {
          if (data.state) {
            this.$set(this, 'totalFee', data.data.info.total_fee)
            this.$set(this, 'discountFee', data.data.info.discount_fee)
            this.points = data.data.info.points
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
        .catch(err => console.log(err))
    }
  },
  methods: {
    getDatas () {
      getCartProductList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'cartList', data.data.list)
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
        .catch(err => console.log(err))
    },
    bill () {
      if (!this.playList.length) {
        return this.$toast('请选择要结算的商品')
      }
      let datas = {}
      let _playList = this.playList
      for (let i in _playList) {
        for (let j in _playList[i]) {
          datas[j] = _playList[i][j]
        }
      }
//      console.log(JSON.stringify(datas))
//      setLocalStorage('orderInfo')
      mallOrderGetPrice(JSON.stringify(datas))
        .then(data => {
//          console.log(data)
          if (data.state) {
            setLocalStorage('orderInfo', JSON.stringify(data.data))
            setLocalStorage('detail', JSON.stringify(datas))
//            this.$router.replace('/mall/pay/?kjljkjklj')
            this.$router.replace('/mall/confirm')
          } else {
            this.$toast(data.errormsg || '网络错误，请重试')
          }
        })
        .catch(err => console.log(err))
    },
    selectAddr () {
      this.popupVisible = true
    }
  },
  components: {
    AppHeader,
    CartItem,
    CartAddressPopup,
    AddressAdd
  }
}
</script>

<style lang="less" scoped>
  .title{
    display: block;
    margin-top: .77em;
    margin-bottom: .3em;
    padding-left: 15px;
    padding-right: 15px;
    color: #999;
    font-size: 14px
  }
  .cart{
    background-color: #fff;
    padding-top: 50px;
    padding-bottom: 55px;
    .cart-main{
      .price{
        padding: 1rem;
        span{
          em{
            font-style: normal;
            color: maroon;
            font-size: 16px;
            font-weight: 600;
            &:before{
              content: "￥";
              font-size: 13px;
            }
          }
        }
      }
      .address-cont{
        padding: .6rem;
        text-align: left;
        border-bottom: 1px solid #f4f4f4;
        position: relative;
        .cont{
          display: flex;
          font-size: 13px;
          position: relative;
          .w-2{
            width: 4rem;
            color: rgba(0, 0, 0, 0.54);
          }
          .f-l{
            flex: 1;
            color: rgba(0, 0, 0, 0.87);
          }
        }
      }
      .btn-box{
        padding: 1rem 0;
        .btn{
          display: block;
          width: 20%;
          margin: 0 auto;
          text-align: center;
          padding: 7px 0;
          background-color: rgb(249, 91, 7);
          color: rgb(255, 255, 255);
        }
        .n-btn{
          background-color: #ffdab9 !important;
        }
      }
    }
  }
  .not{
    &:before{
      content: " " !important;
    }
  }
</style>



