<template>
  <div class="wrap">
    <div class="input-sub" @click="sub">-</div>
    <div class="input-num">
      <span>{{goods.amount}}</span>
    </div>
    <div class="input-add" @click="add">+</div>
  </div>
</template>

<script>
import bus from '../../../assets/js/bus'
export default {
  name: 'Input-Number',
  props: {
    goods: '',
    index: ''
  },
  beforeDestory () {
    bus.$off('subNum')
    bus.$off('addNum')
  },
  methods: {
    sub () {
      bus.$emit('subNum', this.goods, this.index)
    },
    add () {
      bus.$emit('addNum', this.goods, this.index)
    }
  }
}
</script>

<style lang="less" scoped>
  .wrap{
    display: inline-block;
    vertical-align: middle;
    border: 1px solid #eee;
    .input-sub{
      padding: .4rem .7rem;
      text-align: center;
      display: inline-block;
      background-color: #f4f4f4;
      transition: background-color .3s linear 0s;
    }
    .input-num{
      vertical-align: middle;
      display: inline-block;
      padding: 0 .5rem;
      text-align: center;
      span{
        font-size: .9rem;
        color: #51555a;
      }
    }
    .input-add{
      display: inline-block;
      padding: .4rem .7rem;
      text-align: center;
      background-color: #f4f4f4;
      transition: background-color .3s linear 0s;
    }
  }
</style>



