<template>
  <div class="item-wrap">
    <div class="sele" v-if="product.sale || product.unit_id">
      <input type="checkbox" v-model="value1" @change="selectProduct">
    </div>
    <div v-else style="padding: 0.2rem"></div>
    <div class="img">
      <img :src="product.imgurl || '/static/img/default-bg.jpg'" alt="" style="width: 100%;">
    </div>
    <div class="cont">
      <div class="name">
        {{product.name}}
      </div>
      <div class="price">
        <span>售价:</span>
        <span>{{product.total_fee_product}}元</span>
      </div>
      <div v-if="product.integral" class="price">
        <span>积分:</span>
        <span>{{product.integral}}</span>
      </div>
      <div class="num" v-if="product.sale || product.unit_id">
        <input-number
          :goods="product"
          :index="index"
        ></input-number>
        <div class="delete" @click="removeProduct">
          <i class="fa fa-trash-o" aria-hidden="true"></i>
        </div>
      </div>
      <div class="num" v-else>
        <span >已失效</span>
        <div class="delete" @click="removeProduct">
          <i class="fa fa-trash-o" aria-hidden="true"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import InputNumber from './input-number.vue'
export default {
  name: 'Mall-Cart-Item',
  data () {
    return {
      value1: false
    }
  },
  props: {
    product: '',
    index: ''
  },
  watch: {
    value1 (val) {
      this.$parent.$emit('selectOneProduct', this.product, this.index, val)
    }
  },
  methods: {
    removeProduct () {
      this.$parent.$emit('removeProductInCart', this.product)
    }
  },
  components: {
    InputNumber
  }
}
</script>

<style lang="less" scoped>
  .item-wrap{
    padding: .6rem 0;
    border-bottom: 1px solid #f6f6f6;
    background: #FFF;
    display: box;
    display: -webkit-box;
    -webkit-box-pack: center;
    .sele{
      width: 2rem;
      padding: 0 .2rem;
      height: 5.8rem;
      input {
        display: inline-block;
        width: 0;
        margin-top: 10px;
        margin-left: 5px;
        text-align: left;
        -webkit-box-sizing: border-box;
        &:before{
          content: " ";
          display: inline-block;
          width: 1.5rem;
          height: 1.5rem;
          background: #EEE;
          vertical-align: middle;
          -webkit-border-radius: 50%;
          margin-right: 10px;
          -webkit-box-sizing:border-box;
          -webkit-transition:background ease-in .5s
        }
      }
      input[type="checkbox"]:checked::before{
        background-color: rgb(53, 183, 111);
        border: 6px #EEEEEE solid;
      }
    }
    .img{
      width: 4.8rem;
      height: 4.8rem;
      margin: 0 .8rem;
      margin-left: 0;
      position: relative;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .cont{
      flex: 1;
      .name{
        font-size: 14px;
        color: #666;
        margin-bottom: .1rem;
        margin-right: .4rem;
      }
      .price{
        font-size: 13px;
        color: #999;
        margin-bottom: .1rem;
      }
      .num{
        .delete{
          font-size: 1.5rem;
          float: right;
          margin-right: .6rem;
          color: #999;
        }
      }
    }
  }
</style>



